"use client";
import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { notFound } from 'next/navigation';
import { getCertificateBySlug } from '../../../data/certificates';

interface PageProps {
  params: { slug: string };
}

export default function CertificateDetailPage({ params }: PageProps) {
  const cert = getCertificateBySlug(params.slug);
  if (!cert) return notFound();

  return (
    <main className="px-4 sm:px-8 py-6 sm:py-10 mx-auto max-w-7xl">
      <div className="mb-4 sm:mb-6">
        <Link href="/certificates" className="text-sm text-blue-500 hover:underline">‚Üê Back to Certificates</Link>
      </div>

      <div className="w-full border rounded-lg overflow-hidden bg-gray-50">
        <ImageWithFallback src={cert.imageUrl} alt={`${cert.title} preview`} />
      </div>
    </main>
  );
}

function ImageWithFallback({ src, alt }: { src: string; alt: string }) {
  const [failed, setFailed] = useState(false);

  if (failed) {
    // Plain <img> fallback
    return (
      <div className="w-full h-[80vh] flex items-center justify-center bg-gray-50">
        <img
          src={src}
          alt={alt}
          className="max-h-[80vh] w-auto object-contain"
          onError={() => {/* avoid loops */}}
        />
      </div>
    );
  }

  return (
    <div className="relative w-full h-[80vh]">
      <Image
        src={src}
        alt={alt}
        fill
        className="object-contain"
        sizes="100vw"
        onError={() => setFailed(true)}
      />
    </div>
  );
}
